import BookModel from "../../model/Book";
import NoteModel from "../../model/Note";
import BookmarkModel from "../../model/Bookmark";
import { zipBook, zipConfig } from "./common";

let JSZip = (window as any).JSZip;

export const backup = (
  bookArr: BookModel[],
  notes: NoteModel[],
  bookmarks: BookmarkModel[],
  isSync: boolean
) => {
  return new Promise<Blob | boolean>(async (resolve, reject) => {
    let zip = new JSZip();
    let books = bookArr;
    //01dropbox,2onedrive,3webdav4indexeddbuploads5
    let result = await zipConfig(zip, books, notes, bookmarks);
    if (!result) resolve(false);
    if (!isSync) {
      await zipBook(zip, books);
    }
    zip
      .generateAsync({ type: "blob" })
      .then((blob: any) => {
        resolve(blob);
      })
      .catch((err: any) => {
        console.log(err);
        resolve(false);
      });
  });
};
